import React, { useEffect, useState } from 'react';
import TitleCard from "../../components/Cards/TitleCard"
import SearchBar from "../../components/Input/SearchBar";
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { fetchGetEventListAction } from '../../app/service/action/eventAction';
import { EyeIcon, TrashIcon, PencilIcon } from "@heroicons/react/24/outline";
import Loading from '../../components/Loading/Loading';
import parse from 'html-react-parser';
import { formatDateToIndonesian } from '../../utils/DateUtils';
import { PaginationComponent } from '../../components/Pagination/PaginationComponent';
import { openModal } from '../common/modalSlice';
import { CONFIRMATION_MODAL_CLOSE_TYPES, MODAL_BODY_TYPES } from '../../utils/globalConstantUtil';

const TopSideButtons = ({ status, search }) => {
    const navigate = useNavigate();
    const handleAddNew = () => {
        navigate(`/app/add-event/${status}`, { replace: true });
    };


    return (
        <div className="inline-block float-right">
            <SearchBar placeholderText={'Cari Nama Event'}
                setSearchText={(input) => search(input)} />
            <button className="btn px-6 btn-sm normal-case btn-primary ml-3" onClick={handleAddNew}>Add New</button>
        </div>
    )
}

const INITIAL_OBJECT = {
    size: 10,
    page: 1,
    status: '',
    category_event_id: 2,
    search: '',
}
export const Event = ({ status }) => {
    const dispatch = useDispatch()
    const navigate = useNavigate();
    const [requestParam, setRequestParams] = useState(INITIAL_OBJECT)

    const data = useSelector(({ eventListState }) => eventListState.eventList.data)
    const isLoading = useSelector(({ eventListState }) => eventListState.eventList.isLoading)

    useEffect(() => {
        dispatch(fetchGetEventListAction(requestParam, status)).then()
    }, [dispatch, requestParam, status])

    const handleChange = (event, value) => {
        setRequestParams({ ...requestParam, page: value });
        // @ts-ignore
        dispatch(fetchGetEventListAction(requestParam, status)).then();
    };

    const deleteEvent = (index) => {
        dispatch(openModal({
            title: "Confirmation", bodyType: MODAL_BODY_TYPES.CONFIRMATION,
            extraObject: {
                message: `Apakah anda yakin akan menghapus event ini?`,
                type: CONFIRMATION_MODAL_CLOSE_TYPES.DELETED_EVENT,
                index
            }
        }))
    }

    const handleNavigation = (id, title) => {
        navigate(`/app/registration-event/${id}`, { replace: true, state: { title: title } })
    }
    const handleClick = (url, id) => {
        navigate(`${url}/${status}/${id}`, { replace: true })
    }
    return (
        <TitleCard title={`Data Event ${status}`} topMargin={'mt-2'} TopSideButtons={<TopSideButtons status={status}
            search={(input) => setRequestParams({
                ...requestParam,
                search: input
            })} />}>
            <div>
                {isLoading ? <Loading /> :
                    <div className="overflow-x-auto w-full">
                        <table className="table table-zebra table-pin-rows w-full">
                            <thead>
                                <tr>
                                    <th>No</th>
                                    <th>Tanggal</th>
                                    <th>Nama event</th>
                                    <th>Note</th>
                                    <th width={'5%'}>Action</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    data.result ? data.result.map((value, k) => {
                                        return (
                                            <tr key={k}>
                                                <td>{k + 1}</td>
                                                <td>{formatDateToIndonesian(value.date)}</td>
                                                <td>{value.name}</td>
                                                <td>{value.note && parse(value.note)}</td>
                                                <td>
                                                    {/*<Link to={`/app/registration-event/${value.id}`}>*/}
                                                    <button className="btn btn-square btn-ghost"
                                                        onClick={() => handleNavigation(value.id, value.name)}>
                                                        <EyeIcon className="w-5" color={'blue'} />
                                                    </button>
                                                    {/*</Link>*/}
                                                    <button className="btn btn-square btn-ghost"
                                                        onClick={() => handleClick('/app/edit-event', value.id)}>
                                                        <PencilIcon className="w-5" color={'blue'} />
                                                    </button>
                                                    <button className="btn btn-square btn-ghost"
                                                        onClick={() => deleteEvent(value.id)}>
                                                        <TrashIcon className="w-5" color={'red'} />
                                                    </button>
                                                </td>
                                            </tr>
                                        )
                                    }) : null
                                }
                            </tbody>
                        </table>
                        <PaginationComponent data={data} handleChange={handleChange} page={requestParam.page} />
                    </div>
                }
            </div>
        </TitleCard>
    )
}
